import isSelectionInHeading from './isSelectionInHeading';

export default function isSelectionInAnyHeading(value) {
  if (isSelectionInHeading(value, 'heading-1')) return true;
  if (isSelectionInHeading(value, 'heading-2')) return true;
  if (isSelectionInHeading(value, 'heading-3')) return true;
  if (isSelectionInHeading(value, 'heading-4')) return true;
  if (isSelectionInHeading(value, 'heading-5')) return true;
  if (isSelectionInHeading(value, 'heading-6')) return true;
  return false
}
